import axios from 'axios'
	export default {
		props: {
			school: {
				type: Object,
				required: true
			}
		},
		data() {
			return {
				timezones: []
			}
		},
		created: function () {
			axios.get('/api/schools/getTimezones')
				.then(response => {
					this.timezones = response.data.map(function(value) {
						return { value: value.abbr, text: value.text }
					})
				})
				.catch(err => {
					console.log(err)
				})
		},
		methods: {
			submit(e) {
				e.preventDefault()
				this.$parent.$emit('submit', this.school)
			}
		}
	}


